import { Button, Col, Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { MailAppService } from "../../../services/adminService";
import { MyFormItemInput, MyFormItemSelect } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import SearchComponent from "../../core/SearchComponent";

export default function ManageMailAppModifyComponent(props: any) {
    const { setIsLoading } = props;
    const [name, setName] = useState('');
    const [comment, setComment] = useState('');
    const [keySecret, setKeySecret] = useState('');
    const [status, setStatus] = useState(0);
    const [optionCheckList, setOptionCheckList] = useState<any>([]);

    const [dataSearch, setDataSearch] = useState<any>([]);
    const [resultKey, setResultKey] = useState('');
    const [resultName, setResultName] = useState('');

    const options = [
        {
            key: "active",
            label: "Đang hoạt động",
            value: 1
        },
        {
            key: "inactive",
            label: "Không hoạt động",
            value: 0
        }
    ]
    const loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            setIsLoading(true);
            MailAppService.GetDetail(props.keyData, (data: any) => {
                var row = data.data.data;
                setName(row.name);
                setComment(row.comment);
                setKeySecret(row.keySecret);
                setStatus(row.status);
                setOptionCheckList(row.templates);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.keyData, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function onClose() {
        props.onClose();
        setResultKey('');
        setResultName('');
    }

    function handleSearch(data: any) {        
        setIsLoading(true);
        MailAppService.SearchTemplate(data, props.keyData, (data: any) => {
            setDataSearch(data.data.data);
        }, undefined, () => {
            setIsLoading(false);
        });
    }

    function handleSubmitForm() {
        props.setIsLoading(true);
        var body = {
            name: name,
            comment: comment,
            status: status
        };

        if (props.keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        MailAppService.Add(body, (data: any) => {
            props.loadData();
            Fn.PrintMsgSuccess(data);
        }, () => {
            props.setIsLoading(false);
        })
    }

    function Edit(body: any) {
        var bodyTemplate = resultName.split(", ").map((data, index) => {
            return {
                appKey: props.keyData,
                templateCode: data
            }
        })

        MailAppService.AssignTemplates(bodyTemplate, (data: any) => {
            console.log(data.data.message);
        })

        MailAppService.GetDetail(props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            MailAppService.Edit(props.keyData, body, (data1: any) => {
                props.loadData();
                Fn.PrintMsgSuccess(data1);
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function handleDeleteTemplate(key: number) {
        MailAppService.RemoveAssignTemplates(key, (data: any) => {
            loadData();
        })
    }
    return (
        <Drawer
            title={props.keyData === undefined ? "Thêm app mới" : "Cập nhật thông tin app"}
            visible={props.visible}
            onClose={onClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItemInput
                    label="Tên App"
                    value={name}
                    setValue={setName} />

                <MyFormItemInput
                    label="Chú thích"
                    value={comment}
                    setValue={setComment} />

                <MyFormItemInput
                    label="Mã bí mật"
                    readOnly={true}
                    value={keySecret}
                    setValue={setKeySecret} />

                <MyFormItemSelect
                    label="Trạng thái"
                    value={status}
                    setValue={setStatus}
                    options={options} />

                <SearchComponent
                    label="Chọn template"
                    muityChoose={true}
                    onSearch={handleSearch}
                    dataSearch={dataSearch}
                    resultKey={resultKey}
                    resultName={resultName}
                    setResultKey={setResultKey}
                    setResultName={setResultName}
                    isLoading={props.isLoading} />

                <Form.Item>
                    {optionCheckList.map((data: any, index: any) => {
                        return (
                            <Col key={index} span={24}>
                                {data.code}
                                <MyButton
                                    value="Xóa"
                                    type="link"
                                    onClick={() => {
                                        handleDeleteTemplate(data.id)
                                    }} />
                            </Col>
                        );
                    })}
                </Form.Item>
                <Form.Item>
                    <Button loading={props.isLoading} htmlType="submit">
                        {props.keyData === undefined ? "Thêm app mới" : "Cập nhật thông tin app"}
                    </Button>
                </Form.Item>
            </Form >
        </Drawer >
    );
}