import { useState, useCallback, useEffect } from "react";
import { Badge, Button, Popconfirm } from "antd";
import { Fn } from "../../../helper/function";
import { MailAppService } from "../../../services/adminService";
import ManageMailAppModifyComponent from "./MailApp_ModifyComponent";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { MyButton } from "../../core/MyButton";
import TableComponent from "../../core/TableNewComponent";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";
import { MyBox } from "../../core/MyBox";
export default function ManageMailAppIndexComponent() {
    const column = [
        new TableColumn({ title: "STT", key: "stt" }),
        new TableColumn({ title: "Tên", key: "name" }),
        new TableColumn({ title: "Chú thích", key: "comment" }),
        new TableColumn({ title: "Key", key: "secret" }),
        new TableColumn({
            title: "Trạng thái", key: "status", render: (row: number) => {
                var badge: any = {};
                if (row === 1) {
                    badge = {
                        msg: 'Đang hoạt động',
                        color: 'blue'
                    };
                } else {
                    badge = {
                        msg: 'Không hoạt động',
                        color: 'red'
                    };
                }
                return <Badge text={badge.msg} color={badge.color} />
            }
        }),
        new TableColumn({
            title: "Ngày thêm", key: "createdAt", render: (row: any, data: any) => {
                return (<UIFormatDateTimeVNComponent value={row} />);
            }
        }),
        new TableColumn({
            title: "Ngày cập nhật cuối", key: "updatedAt", render: (row: any, data: any) => {
                return (<UIFormatDateTimeVNComponent value={row} />);
            }
        }),
        new TableColumn({
            title: "Hành động", key: "action", render: (row: any, data: any) => {
                return (
                    <div>
                        <MyButton
                            value="Chỉnh sửa"
                            type="link"
                            onClick={() => {
                                handleClickEditApp(data.key)
                            }} />

                        <Popconfirm
                            title="Are you sure to delete this app?"
                            onConfirm={() => {
                                handleClickDeleteApp(data.key);
                            }}>
                            <MyButton value="Xóa" type="link" />
                        </Popconfirm>
                    </div>
                );
            }
        }),
    ];

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [visibleAdd, setVisibleAdd] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState(undefined);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let pagination = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        MailAppService.GetList(pagination, (data: any) => {
            setPageInfo(new PageInfoModel(data));
            var pagedData = data.data.data.pagedData.map((row: any, index: number) => {
                return {
                    stt: index + 1,
                    key: row.key,
                    name: row.name,
                    comment: row.comment,
                    secret: row.keySecret,
                    status: row.status,
                    createdAt: row.createdAt,
                    updatedAt: row.updatedAt
                };
            })
            setData(pagedData);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleOnCloseAdd() {
        setVisibleAdd(false);
    }

    function handleOnCloseEdit() {
        setKeyEdit(undefined);
        setVisibleEdit(false);
    }

    function handleClickAddApp() {
        setVisibleAdd(true);
    }

    function handleClickEditApp(key: any) {
        setKeyEdit(key);
        setVisibleEdit(true);
    }

    function handleClickDeleteApp(key: any) {
        setIsLoading(true);
        MailAppService.GetDetail(key, (data: any) => {
            MailAppService.Delete(key, data.data.data.timestamp, (data: any) => {
                Fn.PrintMsgSuccess(data);
                loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title="Quản lí app mail"
            isLoading={isLoading}>
            <div>
                <Button onClick={handleClickAddApp}>Thêm app mới</Button>
                <TableComponent
                    pageInfo={pageInfo}
                    setPageInfo={setPageInfo}
                    column={column}
                    dataSource={data}
                    loading={isLoading}
                    loadData={loadData}
                    orderBy={orderBy}
                    setOrderBy={setOrderBy} />

                <ManageMailAppModifyComponent
                    keyData={undefined}
                    loadData={loadData}
                    isLoading={isLoading}
                    setIsLoading={setIsLoading}
                    visible={visibleAdd}
                    onClose={handleOnCloseAdd} />

                <ManageMailAppModifyComponent
                    keyData={keyEdit}
                    loadData={loadData}
                    isLoading={isLoading}
                    setIsLoading={setIsLoading}
                    visible={visibleEdit}
                    onClose={handleOnCloseEdit} />
            </div>
        </MyBox>
    );
}